/* ==== Loader ==== */
#page-loader { 
	position: fixed; 
	top: 0; 
	left: 0; 
	height: 100%; 
	width: 100%; 
	background: white; 
	z-index: 9999; 
}
#page-loader img {
	margin: 100px auto; 
	display: block;
}

.loader { 
	text-align: center; 
	margin: auto; 
	position: absolute; 
	top: 0; 
	bottom: 0; 
	left: 0; 
	right: 0;
	height: 40px;
}
.loader { text-align: center; margin: auto; position: absolute; top: 0; bottom: 0; left: 0; right: 0; height: 40px; }
.loader span { display: block; font: 14px "Montserrat", sans-serif; text-transform: uppercase; margin-bottom: 20px; }

.spinner { margin: 0 auto; width: 40px; height: 40px; position: relative; }

.con1 > div, .con2 > div, .con3 > div { width: 7px; height: 7px; border-radius: 100%; position: absolute; -webkit-animation: bouncedelay 1.2s infinite ease-in-out; animation: bouncedelay 1.2s infinite ease-in-out; /* Prevent first frame from flickering when animation starts */ -webkit-animation-fill-mode: both; animation-fill-mode: both; }

.spinner .spinner-container { position: absolute; width: 100%; height: 100%; }

.con2 { -webkit-transform: rotateZ(45deg); transform: rotateZ(45deg); }

.con3 { -webkit-transform: rotateZ(90deg); transform: rotateZ(90deg); }

.circle1 { top: 0; left: 0; }

.circle2 { top: 0; right: 0; }

.circle3 { right: 0; bottom: 0; }

.circle4 { left: 0; bottom: 0; }

.con2 .circle1 { -webkit-animation-delay: -1.1s; animation-delay: -1.1s; }

.con3 .circle1 { -webkit-animation-delay: -1.0s; animation-delay: -1.0s; }

.con1 .circle2 { -webkit-animation-delay: -0.9s; animation-delay: -0.9s; }

.con2 .circle2 { -webkit-animation-delay: -0.8s; animation-delay: -0.8s; }

.con3 .circle2 { -webkit-animation-delay: -0.7s; animation-delay: -0.7s; }

.con1 .circle3 { -webkit-animation-delay: -0.6s; animation-delay: -0.6s; }

.con2 .circle3 { -webkit-animation-delay: -0.5s; animation-delay: -0.5s; }

.con3 .circle3 { -webkit-animation-delay: -0.4s; animation-delay: -0.4s; }

.con1 .circle4 { -webkit-animation-delay: -0.3s; animation-delay: -0.3s; }

.con2 .circle4 { -webkit-animation-delay: -0.2s; animation-delay: -0.2s; }

.con3 .circle4 { -webkit-animation-delay: -0.1s; animation-delay: -0.1s; }

@-webkit-keyframes bouncedelay { 0%, 80%, 100% { -webkit-transform: scale(0); }
  40% { -webkit-transform: scale(1); } }

@keyframes bouncedelay { 0%, 80%, 100% { transform: scale(0); -webkit-transform: scale(0); }
  40% { transform: scale(1); -webkit-transform: scale(1); } }

@-webkit-keyframes boxSpin{
  0%{
    -webkit-transform: translate(-10px, 0px);
            transform: translate(-10px, 0px);
  }
  25%{
    -webkit-transform: translate(10px, 10px);
            transform: translate(10px, 10px);
  }
  50%{
    -webkit-transform: translate(10px, -10px);
            transform: translate(10px, -10px);
  }
  75%{
    -webkit-transform: translate(-10px, -10px);
            transform: translate(-10px, -10px);
  }
  100%{
    -webkit-transform: translate(-10px, 10px);
            transform: translate(-10px, 10px);
  }
}

@keyframes boxSpin{
  0%{
    -webkit-transform: translate(-10px, 0px);
            transform: translate(-10px, 0px);
  }
  25%{
    -webkit-transform: translate(10px, 10px);
            transform: translate(10px, 10px);
  }
  50%{
    -webkit-transform: translate(10px, -10px);
            transform: translate(10px, -10px);
  }
  75%{
    -webkit-transform: translate(-10px, -10px);
            transform: translate(-10px, -10px);
  }
  100%{
    -webkit-transform: translate(-10px, 10px);
            transform: translate(-10px, 10px);
  }
}


@-webkit-keyframes shadowSpin{
  0%{
    box-shadow: 10px -10px #3fBBC0, -10px 10px #FFDC00;
  }
  25%{
    box-shadow: 10px 10px #3fBBC0, -10px -10px #FFDC00;
  }
  50%{
    box-shadow: -10px 10px #3fBBC0, 10px -10px #FFDC00;
  }
  75%{
    box-shadow: -10px -10px #3fBBC0, 10px 10px #FFDC00;
  }
  100%{
    box-shadow: 10px -10px #3fBBC0, -10px 10px #FFDC00;
  }
}


@keyframes shadowSpin{
  0%{
    box-shadow: 10px -10px #3fBBC0, -10px 10px #FFDC00;
  }
  25%{
    box-shadow: 10px 10px #3fBBC0, -10px -10px #FFDC00;
  }
  50%{
    box-shadow: -10px 10px #3fBBC0, 10px -10px #FFDC00;
  }
  75%{
